}

body {
  font-family: 'Open Sans', sans-serif;
  font-weight: 700; }

.leaflet-container {
  height: 100vh;
  left: 0;
  position: absolute !important;
  right: 0;
  top: 0%;
  -webkit-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0); }
  .leaflet-container.show-content {
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0); }

.list-popup {
  list-style: none;
  padding-left: 0; }
  .list-popup li {
    display: inline-block;
    text-align: center;
    width: 100%; }
    .list-popup li.title {
      font-size: 18px;
      font-weight: bold; }

.img-wiki {
  position: absolute;
  z-index: 1000;
  width: 110px;
  bottom: 30px;
  left: 30px; }

.content {
  background: #FFF;
  position: absolute;
  right: 0;
  top: 190px;
  width: 50%;
  z-index: 1000;
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0); }
  .content.show-content {
    -webkit-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0); }
  .content .content-box {
    height: 400px;
    overflow-y: scroll;
    padding: 40px 30px 0px 30px; }

table.results {
  border-collapse: collapse;
  text-align: left; }
  table.results thead tr {
    border: 1px solid #eee;
    background: #CCC;
    color: #FFF; }
    table.results thead tr th {
      border-right: 1px solid #eee;
      padding: 10px 20px; }
      table.results thead tr th.center {
        text-align: center; }
      table.results thead tr th:last-child {
        border-right: 0 none; }
  table.results tbody tr {
    border: 1px solid #ccc; }
    table.results tbody tr td {
      border-right: 1px solid #eee;
      padding: 10px 20px; }
      table.results tbody tr td.center {
        text-align: center; }
      table.results tbody tr td:last-child {
        border-right: 0 none; }

.header {
  background: #17202A;
  border-bottom: 2px solid #F1C40F;
  height: 60px;
  text-align: center;
  padding: 20px 0 10px 0;
  position: fixed;
  width: 100%;
  z-index: 1000; }
  .header img {
    width: 120px; }
    @media only screen and (max-width: 768px) {
      .header img {
        float: left;
        margin-left: 20px; } }
  .header .select-box {
    position: absolute;
    right: 20px;
    top: 0;
    width: 180px; }

.header-menu {
  background: #FFF;
  display: none;
  left: 0;
  padding: 0 40px;
  position: fixed;
  top: 0;
  width: calc(100% - 80px);
  z-index: 1; }
  .header-menu ul {
    float: right;
    list-style: none;
    padding-left: 0; }
    .header-menu ul li {
      display: inline-block;
      margin-left: 20px;
      width: auto; }

.list-circuits {
  border: 1px solid #D6DBDF;
  height: calc(100vh - 180px);
  margin: 0 20px;
  overflow-y: scroll;
  padding: 0 40px; }
  .list-circuits ul {
    list-style: none;
    padding: 0;
    text-align: center;
    width: 100%; }
    .list-circuits ul li {
      margin-bottom: 5px;
      padding: 0 10px; }
      .list-circuits ul li a {
        color: #D6DBDF;
        text-decoration: none; }

.select-year {
  width: 150px;
  position: absolute;
  right: 30px;
  top: 30px;
  z-index: 10000; }
  .select-year img {
    margin-bottom: 20px;
    width: 50%; }
  .select-year select {
    width: 100%; }

.select-circuit {
  width: 150px;
  position: absolute;
  right: 30px;
  top: 30px; }
  .select-circuit img {
    margin-bottom: 20px;
    width: 50%; }
  .select-circuit select {
    width: 100%; }

.carousel {
  position: absolute;
  z-index: 1000;
  bottom: 20px;
  width: 100%;
  left: 0; }
  .carousel ul {
    padding-left: 0;
    margin: 0 auto;
    text-align: center; }
    .carousel ul li {
      margin: 0 10px;
      width: auto;
      display: inline-block; }
      .carousel ul li span {
        cursor: pointer;
        display: inline-block;
        color: #FFF; }
        .carousel ul li span.active {
          color: red; }

